<template>
  <BaseLayout title="积分兑换" subtitle="兑换是用户之间的自由交易行为，每笔交易平台会收取 5% 的手续费" bg="#1989fa" fg="#FFF">

    <div class="info">
      
      <div class="request">
        <van-field type="number" :border="1" v-model="state.amount" input-align="right" clearable label="积分数量" placeholder="拿多少积分去换">
          <template #extra>&nbsp;分</template>
        </van-field>
        <van-field type="number" :border="1" v-model="state.value"  input-align="right" clearable label="现金价值" placeholder="想换成多少现金">
          <template #extra>&nbsp;元</template>
        </van-field>
        <van-field v-model="state.remark" type="text" maxlength="20" input-align="right" label="备注" placeholder="可以不填，仅自己可见" />
        <van-button round block  type="primary" @click="onRequest">发起兑换</van-button>
      </div>

      <van-tabs v-model:active="state.status" color="#1989fa" offset-top="45" type="line" sticky swipeable animated @change="onChange">
        <van-tab title="所有兑换申请"></van-tab>
        <van-tab title="我的兑换记录"></van-tab>
      </van-tabs>

      <van-list v-model:loading="state.loading" :finished="state.finished" loading-text="正在请求数据 ..." finished-text="没有更多了" :immediate-check="false" @load="onLoad">
        <van-empty v-show="state.list.length == 0"></van-empty>
        <van-cell-group class="block" :border="false" v-for="(item,index) in state.list" :key="index">
          <van-cell class="dt" :border="false" :title="item.dt">
            <template #extra>
              <span :style="{color:item.color}">{{item.result}}</span>
              &nbsp;&nbsp;#{{item.id}}
            </template>
          </van-cell>
          <van-cell class="item" :border="false" :title="item.amount + '分&nbsp;&nbsp;→&nbsp;&nbsp;' + item.value + '元'">
            <template #extra>
              <van-button v-show="state.status == 0 && item.ask != uid" type="primary" size="mini" plain @click="onConfirm(item.id)">我换</van-button>
              <van-button v-show="item.status ==  0 && item.ask == uid" type="danger" size="mini" plain @click="onCancel(item.id)">取消</van-button>
            </template>
          </van-cell>
          <van-cell class="remark" v-show="state.status > 0" :border="false" :value="item.remark"></van-cell>
        </van-cell-group>
      </van-list>

    </div>

  </BaseLayout>
</template>

<style scoped>

.info {
  min-height: 580px;
  padding: 10px;
  background: #F9F9F9;
  border-radius: 20px;
  border: solid 5px #1989fa;
}

.request {
  display:flex;
  flex-flow: column nowrap;
  justify-content: space-around;
  margin-bottom: 20px;
  padding: 5px 15px;
  height: 200px;
  background: #FFF;
  border-radius: 10px;
  border: solid 1px #EEE;
  font-size: 14px;
}

.title {
  margin: 30px 5px 0px 5px;
  color: #999;
  border-color: #999;
  font-size: 14px;
}
.block {
  margin-top: 20px;
  padding-bottom: 10px;
  border-radius: 10px;
  overflow: hidden;
}
.dt {
  height: 50px;
  padding-top: 20px;
  font-size: 12px;
}
.item {
  height: 40px;
  font-size: 16px;
  font-weight: 600;
  line-height: 26px;
}
.remark {
  font-size: 12px;
}
</style>

<script>
import { reactive } from 'vue'
import { mapGetters } from 'vuex'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  setup(){
    const state = reactive({

      amount: 100,
      value: 90,
      remark: "",

      status: 0,  // 当前激活的标签
      loading: false,
      finished: false,

      list: [],   // 列表
      offset: 0,  // 记录偏移量
      limit : 20, // 每页记录数量
    });

    return {state};
  },

  created() {
    this.onChange();
  },

  mounted() {
    //this.$store.commit("toggleService", true);
  },

  computed: {
    ...mapGetters(["authorized", "uid"]),
  },

  methods: {
    onChange() {
      this.state.loading = true;
      this.state.finished = false;
      this.state.offset = 0;
      this.state.list = [];
      this.onLoad();
    },
    onLoad() {
      this.$http.get("/property/exchangelist?status=" + this.state.status + "&offset=" + this.state.offset + "&limit=" + this.state.limit).then((data) => {
        if (data.length == 0 || data.length < this.state.limit)
          this.state.finished = true;
        this.state.loading = false;
        this.state.offset += data.length;

        for (let i in data){
          let item = data[i];
          this.state.list.push({...item, ...{
            amount: item.amount.replace(/(\.\d\d)(\d+)$/, "$1"),
            result: ["等待兑换", "已完成", "已取消"][item.status],
            color : ["#33F", "#090", "#F66"][item.status],
          }});
        }
      });
    },

    onRequest(){
      if (!/^[\d]{1,6}$/.test(this.state.amount) || !/^[\d]{1,6}$/.test(this.state.value))
        return this.$toast("填写错误");
      if (this.state.amount - this.state.value < 0)
        return this.$toast("现金价值过高");

      this.$http.post("/property/exchangeRequest?type=point2cash&amount=" + this.state.amount + "&value=" + this.state.value + "&remark=" + this.state.remark).then((data) => {
        this.state.status = 1;
        this.onChange();
      }).catch(this.onChange);
    },

    onConfirm(id){
      this.$http.post("/property/exchangeConfirm?id=" + id).then(this.onChange).catch(this.onChange);
    },

    onCancel(id) {
      this.$http.post("/property/exchangeCancel?id=" + id).then(this.onChange).catch(this.onChange);
    },

  },
}
</script>
